.sidebar{
    background-color: rgb(0, 0, 0);
    grid-area: sidebar;
}
.sidebar a{
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    margin-left: 20px;
    color: #fff;
    text-decoration: none;
    transition: .4s;
}
.sidebar a:hover{
    color: #000000;
}
.friendsBlock{
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
}