.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #000;
    grid-area: header;
}
.header img{
    margin: 2px;
    width: 50px;
}
.a{
    color:white;
    margin-left: 30px;
    transition: .5s;
}
.a:hover{
    color:black;
}
